:root {
  --primary-color: #4285f4;
  --secondary-color: #34a853;
  --danger-color: #ea4335;
  --warning-color: #fbbc05;
  --light-gray: #f5f5f5;
  --medium-gray: #e0e0e0;
  --dark-gray: #757575;
  --text-color: #212121;
  --border-radius: 8px;
  --box-shadow: 0 2px 10px #0000001a;
}

img {
  width: 300px;
  height: 300px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: var(--text-color);
  background-color: #f9f9f9;
  padding: 20px;
  font-family: Roboto, sans-serif;
  line-height: 1.6;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

h1, h2, h3 {
  color: var(--text-color);
  margin-bottom: 20px;
}

button {
  cursor: pointer;
  border-radius: var(--border-radius);
  border: none;
  padding: 10px 15px;
  font-size: 14px;
  transition: all .3s;
}

.create-post-btn {
  background-color: var(--primary-color);
  color: #fff;
  margin-bottom: 20px;
}

.create-post-btn:hover {
  background-color: #3367d6;
}

.edit-btn {
  background-color: var(--warning-color);
  color: #fff;
  margin-right: 5px;
  padding: 5px 10px;
}

.delete-btn {
  background-color: var(--danger-color);
  color: #fff;
  padding: 5px 10px;
}

.posts-list {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 20px;
  list-style: none;
  display: grid;
}

.post-card {
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  background-color: #fff;
  flex-direction: column;
  padding: 20px;
  display: flex;
}

.post-header {
  margin-bottom: 15px;
}

.post-header h3 {
  margin-bottom: 5px;
}

.author {
  color: var(--dark-gray);
  font-size: 14px;
}

.photo-post {
  border-radius: var(--border-radius);
  width: 100%;
  height: auto;
  margin-bottom: 15px;
}

.post-content {
  flex-grow: 1;
  margin-bottom: 15px;
}

.post-footer {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.post-stats {
  align-items: center;
  display: flex;
}

.comments-count {
  color: var(--dark-gray);
  align-items: center;
  font-size: 14px;
  display: flex;
}

.material-icons {
  margin-right: 5px;
  font-size: 18px;
}

.search-container {
  margin-bottom: 20px;
  display: flex;
}

#searchInput {
  border: 1px solid var(--medium-gray);
  border-radius: var(--border-radius) 0 0 var(--border-radius);
  flex-grow: 1;
  padding: 10px;
  font-size: 16px;
}

#searchBtn {
  background-color: var(--dark-gray);
  color: #fff;
  border-radius: 0 var(--border-radius) var(--border-radius) 0;
  padding: 10px 20px;
}

.modal-backdrop {
  z-index: 1000;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  border-radius: var(--border-radius);
  width: 90%;
  max-width: 500px;
  box-shadow: var(--box-shadow);
  background-color: #fff;
  padding: 25px;
}

.modal-form {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.modal-form input, .modal-form textarea {
  border: 1px solid var(--medium-gray);
  border-radius: var(--border-radius);
  padding: 10px;
  font-size: 16px;
}

.modal-form textarea {
  resize: vertical;
  min-height: 100px;
}

.modal-buttons {
  justify-content: flex-end;
  gap: 10px;
  margin-top: 15px;
  display: flex;
}

.modal-submit-btn {
  background-color: var(--secondary-color);
  color: #fff;
}

.modal-cancel-btn {
  background-color: var(--dark-gray);
  color: #fff;
}

.no-posts {
  text-align: center;
  color: var(--dark-gray);
  padding: 20px;
}

@media (width <= 600px) {
  .posts-list {
    grid-template-columns: 1fr;
  }

  .modal {
    width: 95%;
    padding: 15px;
  }
}
